import _ from 'lodash';
import type { DeepKey } from './deep-utils';

export type DataPath = DeepKey[];

/**
 * 
 */
export interface ParsedItem<T = any> {
  /**  */
  label: string;
  /**  */
  value: T;
  /**  */
  path: DataPath;
  /**  */
  isLeaf: boolean;
  /**  */
  canRename: boolean;
}

/**
 *  SillyTavern 
 *
 * - label=
 * - label=
 * - 
 * - 
 *
 * @param node    
 * @param basePath 
 */
export function expandArrayLike(
  node: any,
  basePath: DataPath = [],
): ParsedItem[] {
  if (node == null) return [];

  const results: ParsedItem[] = [];

  if (Array.isArray(node)) {
    node.forEach((element, index) => {
      const elementPath = [...basePath, index] as DataPath;

      if (_.isString(element)) {
        results.push({
          label: element,
          value: element,
          path: elementPath,
          isLeaf: true,
          canRename: false,
        });
        return;
      }

      if (_.isPlainObject(element)) {
        const keys = Object.keys(element as Record<string, unknown>);

        if (keys.length === 1) {
          const key = keys[0];
          const value = (element as any)[key];
          results.push({
            label: key,
            value,
            path: [...elementPath, key] as DataPath,
            isLeaf: _.isString(value),
            canRename: true,
          });
          return;
        }

        if (keys.length > 1) {
          keys.forEach(key => {
            const value = (element as any)[key];
            results.push({
              label: key,
              value,
              path: [...elementPath, key] as DataPath,
              isLeaf: _.isString(value),
              canRename: true,
            });
          });
          return;
        }
      }

      results.push({
        label: _.isString(element) ? element : String(index),
        value: element,
        path: elementPath,
        isLeaf: _.isString(element),
        canRename: false,
      });
    });

    return results;
  }

  if (_.isPlainObject(node)) {
    return Object.keys(node as Record<string, unknown>).map(key => {
      const value = (node as any)[key];
      return {
        label: key,
        value,
        path: [...basePath, key] as DataPath,
        isLeaf: _.isString(value),
        canRename: true,
      };
    });
  }

  return results;
}
