an v-if=\"editMode\" class=\"drag-handle-l1 shrink-0 cursor-move\" title=\"\">  </span>\n            <span\nrole=\"tab\" class=\"kk-text-lg tab cursor-pointer px-0! whitespace-nowrap text-(--quick-send-text)\"\n              :class=\"{ 'tab-active': activeTabIndex === idx }\" :title=\"entry.label\" @click=\"selectLevel1(idx)\">\n              {{ entry.label }}\n            </span>\n            <template v-if=\"editMode\">\n              <ReuseActionMenu\n                :on-edit=\"() => openEdit(idx, 'l1')\"\n                :on-delete=\"() => (confirmDeleteId = makeIdForLevel1Idx(idx))\"\n                :on-copy=\"() => handleCopy(idx, 'l1')\"\n              />\n            </template>\n          </div>\n          <span\nv-if=\"editMode\"\n            class=\"ml-1 shrink-0 cursor-pointer rounded-sm border border-dashed border-(--quick-send-text) px-1 py-0\"\n            title=\"\" @click.stop=\"openCreate('l1')\">\n            +\n          </span>\n        </div>\n      </div>\n\n      <!-- TAB -->\n      <div class=\"overflow-x-auto overflow-y-hidden bg-(--quick-send-bg-sec) px-1\">\n        <div class=\"tabs-border tabs flex items-center\" role=\"tablist\">\n          <div ref=\"level2Container\" class=\"level2Container flex flex-nowrap items-center gap-1\">\n            <div\nv-for=\"(entry, idx) in level2Entries\" :key=\"makeIdForLevel2Idx(idx)\"\n              class=\"flex flex-nowrap items-center gap-0.5\">\n              <span v-if=\"editMode\" class=\"drag-handle-l2 shrink-0 cursor-move\" title=\"\">  </span>\n              <div\nrole=\"tab\"\n                class=\"kk-text-base tab h-2.5 cursor-pointer px-0! whitespace-nowrap text-(--quick-send-text)\"\n                :class=\"{ 'tab-active': activeSecondIndex === idx }\" @click=\"selectLevel2(idx)\">\n                {{ entry.label }}\n              </div>\n              <template v-if=\"editMode\">\n                <ReuseActionMenu\n                  :on-edit=\"() => openEdit(idx, 'l2')\"\n           