               </div>\n          <div\nv-if=\"editMode\" class=\"\n  auto-grid-item flex cursor-pointer items-center justify-center rounded border border-dashed px-0.5 py-0.5\n\" title=\"\" @click=\"openCreate('grid')\">\n            +\n          </div>\n        </div>\n      </div>\n\n      <!--  -->\n      <div\n        class=\"m-1 flex flex-col gap-0.5 rounded border border-(--quick-send-border-color) bg-(--quick-send-bg) p-0.75\">\n        <!-- / -->\n        <DefinePrefixSuffixList\nv-slot=\"{\n          list,\n          selectedIndexes,\n          dragHandle,\n          keyPrefix,\n          onItemClick,\n          containerRef,\n          containerClass,\n          onAddClick,\n        }\">\n          <div class=\"max-h-10 overflow-y-auto rounded\">\n            <div\n:ref=\"containerRef\" :class=\"containerClass\" class=\"auto-grid grid gap-0.5\"\n              style=\"--auto-grid-min: calc(var(--spacing) * 8)\">\n              <div\nv-for=\"(item, idx) in list\" :key=\"`${keyPrefix}-${idx}`\"\n                class=\"auto-grid-item kk-text-sm cursor-pointer rounded bg-(--quick-send-bg-sec) p-0.5\"\n                :class=\"{ 'bg-(--quick-send-primary-color)/70': selectedIndexes.has(idx) }\"\n                :title=\"getPrefixSuffixValue(item)\" @click=\"onItemClick(item, idx)\">\n                <span v-if=\"editMode\" :class=\"dragHandle\" class=\"cursor-move\" title=\"\">  </span>\n                <span class=\"grid-label\"> {{ getPrefixSuffixKey(item) }}</span>\n                <template v-if=\"editMode\">\n                  <ReuseActionMenu\n                    :on-edit=\"() => openEditPrefixSuffix(idx, keyPrefix as any)\"\n                    :on-delete=\"() => (confirmDeleteId = `${keyPrefix}:${idx}`)\"\n                    :on-copy=\"() => handleCopyPrefixSuffix(idx, keyPrefix as any)\"\n                  />\n                </template>\n              </div>\n              <div\nv-if=\"editMode\" class=\"\n  auto-grid-item kk-text-sm flex cursor-pointer items-center justify-center rounded border border-dashed p-0.5\n\" title=\"\" @click=\"onAddClick\">\n                +\n              </div>\n            </div>\n          </div>\n        </DefinePrefixSuffixList>\n\n        <!--  -->\n        <div v-if=\"showPrefixList\">\n          <ReusePrefixSuffixList\n:list=\"prefixList\" :selected-indexes=\"selectedPrefixIndexes\"\n            drag-handle=\"drag-handle-prefix\" container-class=\"prefixContainer\" key-prefix=\"prefix\"\n            :on-item-click=\"onPrefixItemClick\" :container-ref=\"(el: any) => (prefixContainer = el)\"\n            :on-add-click=\"() => openCreatePrefixSuffix('prefix')\" />\n        </div>\n\n        <!--  -->\n        <div v-if=\"showSuffixList\">\n          <ReusePrefixSuffixList\n:list=\"suffixList\" :selected-indexes=\"selectedSuffixIndexes\"\n            drag-handle=\"drag-handle-suffix\" container-class=\"suffixContainer\" key-prefix=\"suffix\"\n            :on-item-click=\"onSuffixItemClick\" :container-ref=\"(el: any) => (suffixContainer = el)\"\n            :on-add-click=\"() => openCreatePrefixSuffix('suffix')\" />\n        </div>\n        <div class=\"flex items-center justify-between\">\n          <div class=\"flex gap-0.5\">\n            <div\nclass=\"\n  flex cursor-pointer flex-wrap items-center justify-center gap-0.25 rounded border-none bg-(--quick-send-bg-sec) px-1\n  py-0.5\n\" :class=\"{ 'bg-(--quick-send-primary-color)/70': showPrefixList }\" @click=\"togglePrefixList\">\n              <span class=\"icon-[bxs--left-arrow-square]\"></span><span class=\"whitespace-nowrap\"></span>\n            </div>\n            <div\nclass=\"\n  flex cursor-pointer flex-wrap items-center justify-center gap-0.25 rounded border-none bg-(--quick-send-bg-sec) px-1\n  py-0.5\n\" :class=\"{ 'bg-(--quick-send-primary-color)/70': showSuffixList }\" @click=\"toggleSuffixList\">\n              <span class=\"icon-[bxs--right-arrow-square]\"></span><span class=\"whitespace-nowrap\"></span>\n  