  v-if=\"editOpen\"\n    v-model=\"editOpen\"\n    :snapshot=\"editSnapshot\"\n    @save=\"handleEditSave\"\n  />\n  <FixedPrefixSuffixModal\n    v-if=\"fixedPrefixSuffixModalOpen\"\n    v-model=\"fixedPrefixSuffixModalOpen\"\n    :data=\"fixedPrefixSuffixData\"\n    @save=\"handleFixedPrefixSuffixSave\"\n  />\n  <template v-if=\"confirmDeleteId !== null\">\n    <div\n      class=\"modal-open kakaa-custom-tailwind modal absolute z-10000\"\n    >\n      <!-- prettier-ignore-attribute -->\n      <div\n        class=\"\n          flex h-fit max-h-[60dvh] w-[80dvw] max-w-[500px] flex-col items-center justify-center rounded-sm\n          bg-(--quick-send-bg) p-1\n        \"\n      >\n        <div class=\"kk-text-lg shrink-0 text-center font-bold\">\n          \n        </div>\n        <div class=\"modal-action mt-1 shrink-0\">\n          <button\n            class=\"btn btn-xs btn-error\"\n            @click=\"handleDeleteById(confirmDeleteId!)\"\n          >\n            \n          </button>\n          <button class=\"btn btn-xs\" @click=\"confirmDeleteId = null\">\n            \n          </button>\n        </div>\n      </div>\n    </div>\n  </template>\n</template>\n\n<script lang=\"ts\" setup>\nimport {\n  createReusableTemplate,\n  useLocalStorage,\n} from '@vueuse/core';\nimport _ from 'lodash';\nimport {\n  computed,\n  inject,\n  nextTick,\n  onMounted,\n  reactive,\n  ref,\n  toRaw,\n  toRef,\n  watch,\n} from 'vue';\nimport { Tippy } from 'vue-tippy';\nimport EditKeyModal, { type EditSnapshot } from './EditKeyModal.vue';\nimport FixedPrefixSuffixModal, {\n  type FixedPrefixSuffixData,\n} from './FixedPrefixSuffixModal.vue';\nimport { expandArrayLike, type DataPath } from './utils/data-parser';\nimport { getByPath } from './utils/deep-utils';\nimport {\n  isSingleKeyObject as isSingleKeyObjectUtil,\n  updateContainerWithPayload,\n} from './utils/quicksend';\nimport { useShadowDraggableList } from './utils/useShadowDraggable';\n\nconst confirmDeleteId = ref<string | null>(null);\nconst isMobile = computed(() => SillyTavern?.isMobile());\n\n/**\n * /\n */\nconst [DefinePrefixSuffixList, ReusePrefixSuffixList] =\n  createReusableTemplate<{\n    list: any[];\n    selectedIndexes: Set<number>;\n    dragHandle: string;\n    containerClass?: string;\n    keyPrefix: string;\n    onItemClick: (item: any, idx: number) => void;\n    containerRef?: (el: any) => void;\n    onAddClick?: () => void;\n  }>();\n\n// //\nconst [DefineActionMenu, ReuseActionMenu] = createReusableTemplate<{\n  onEdit?: () => void;\n  onDelete?: () => void;\n  onCopy?: () => void;\n}>();\n\n/**\n * \n */\nconst editMode = ref(false);\n\n/**\n * \n */\nconst editOpen = ref(false);\n\n/**\n * \n */\nconst editSnapshot = ref<EditSnapshot | null>(null);\n\n/**\n * ID\n */\nconst editNodeId = ref<string | null>(null);\n\n/**\n * \n */\nconst rawData = reactive(\n  getVariables({ type: 'script', script_id: getScriptId() }),\n);\n\nif (!Array.isArray(rawData.data)) {\n  rawData.data = [];\n  replaceVariables(rawData, {\n    type: 'script',\n    script_id: getScriptId(),\n  });\n}\n\n// if (typeof rawData.showTip !== 'boolean') {\n//   rawData.showTip = true;\n// }\n\nif (\n  rawData.popupSize == null ||\n  typeof rawData.popupSize !== 'object'\n) {\n  rawData.popupSize = {\n    desktop: { width: '50dvw', height: '60dvh' },\n    mobile: { width: '90dvw', height: '80dvh' },\n  };\n  replaceVariables(rawData, {\n    type: 'script',\n    script_id: getScriptId(),\n  });\n}\n\nconst size = computed(() => ({\n  mobileHeight: rawData.popupSize?.mobile?.height,\n  mobileWidth: rawData.popupSize?.mobile?.width,\n  desktopHeight: rawData.popupSize?.desktop?.height,\n  desktopWidth: rawData.popupSize?.desktop?.width,\n}));\n\n/**\n * \n *  toRef  useSortable \n */\nconst quickSendData = toRef(rawData, 'data');\n\n/**\n * \n */\n// const showAlertTip = ref(rawData.showTip);\n\n/**\n *  DOM \n */\n// const alertTip = ref<HTMLElement | null>(null);\n\n/**\n * \n */\n// let alert