!Array.isArray(element) &&\n      isSingleKeyObjectUtil(element)\n    ) {\n      const innerKey = Object.keys(element)[0];\n      const innerValue = (element as any)[innerKey];\n      return {\n        container: element,\n        keyInContainer: innerKey,\n        title: innerKey,\n        value: innerValue,\n        canRename: true,\n        isLeaf: typeof innerValue === 'string',\n        path,\n      };\n    }\n    return {\n      container,\n      keyInContainer: lastKey,\n      title: `#${lastKey}`,\n      value: element,\n      canRename: false,\n      isLeaf: typeof element === 'string',\n      path,\n    };\n  }\n\n  const value = getByPath(root, path);\n  const title =\n    typeof lastKey === 'string'\n      ? lastKey\n      : typeof lastKey === 'number'\n        ? `#${lastKey}`\n        : String(lastKey);\n  const canRename = typeof lastKey === 'string';\n\n  return {\n    container,\n    keyInContainer: lastKey as string | number,\n    title,\n    value,\n    canRename,\n    isLeaf: typeof value === 'string',\n    path,\n  };\n}\nfunction makeIdForLevel1Idx(idx: number): string {\n  const entry = level1Entries.value[idx];\n  const path = entry?.path ?? ([idx] as DataPath);\n  return makeIdFromPath(path);\n}\n\nfunction makeIdForLevel2Idx(idx: number): string {\n  const entry = level2Entries.value[idx];\n  if (!entry) return makeIdForLevel1Idx(activeTabIndex.value);\n  return makeIdFromPath(entry.path);\n}\n\nfunction makeIdForGridIdx(idx: number): string {\n  const baseEntry = level2Entries.value[activeSecondIndex.value];\n  if (!baseEntry) return makeIdForLevel1Idx(activeTabIndex.value);\n  let current: any = baseEntry.value;\n  let currentPath: DataPath = baseEntry.path;\n  for (const stepIdx of gridPath.value) {\n    const step = computeGridEntries(current, currentPath);\n    const meta = step.metas[stepIdx];\n    if (!meta) return makeIdFromPath(currentPath);\n    current = step.values[stepIdx];\n    currentPath = meta.path;\n  }\n  const step = computeGridEntries(current, currentPath);\n  const targetMeta = step.metas[idx];\n  return makeIdFromPath(targetMeta ? targetMeta.path : currentPath);\n}\nfunction openEdit(idx: number, scope: 'l1' | 'l2' | 'grid') {\n  const id =\n    scope === 'grid'\n      ? makeIdForGridIdx(idx)\n      : scope === 'l2'\n        ? makeIdForLevel2Idx(idx)\n        : makeIdForLevel1Idx(idx);\n  const resolved = resolveById(quickSendData.value, id);\n  if (!resolved) return;\n\n  editNodeId.value = id;\n  editSnapshot.value = {\n    title: resolved.title,\n    name: resolved.title,\n    canRename: resolved.canRename,\n    isLeaf: resolved.isLeaf,\n    value: resolved.isLeaf ? (resolved.value as string) : undefined,\n    children: makeChildrenSnapshot(resolved.value),\n  };\n  editOpen.value = true;\n}\n\n/**\n * \n *\n * @param scope - \n */\nfunction openCreate(scope: 'l1' | 'l2' | 'grid') {\n  editNodeId.value = `__create__:${scope}`;\n  editSnapshot.value = {\n    title: '',\n    name: '',\n    canRename: true,\n    isLeaf: false,\n    value: undefined,\n    children: [],\n    isCreate: true,\n    createLevel: scope,\n  };\n  editOpen.value = true;\n}\n\n/**\n * \n *\n * \n * \n *\n * @param payload - \n */\nfunction handleEditSave(payload: {\n  newName?: string;\n  newValue?: string;\n  children: Array<{\n    id: string;\n    newName?: string;\n    newValue?: string;\n  }>;\n  __create?: boolean;\n  __createAsLeaf?: boolean;\n}) {\n  const id = editNodeId.value;\n  if (!id) return;\n\n  if (id.startsWith('__create__:')) {\n    const scope = id.slice(11);\n    const newName = payload.newName || '';\n\n    if (scope === 'prefix') {\n      const newItem = { [newName]: payload.newValue ?? '' };\n      if (!rawData.prefix && !rawData.perfix) {\n        rawData.prefix = [];\n      }\n      const list = rawData.perfix || rawData.prefix;\n      if (Array.isArray(list)) {\n        list.push(newItem);\n      }\n    } else if (scope === 'suffix') {\n      const newItem = { [newName]: payload.newValue ?? '' };\n      if (!rawData.suffix) {\n        rawData.suffix = [];\n      }\n      rawData.suffix.push(newItem);\n    } else if (scope === 'l1') {\n      const newItem = { [newName]: [] };\n      quickSendData.value.push(newItem);\n    } else if (scope === 'l2') {\n      const level1Entry = level1Entries.value[activeTabIndex.value];\n      if (level1Entry && Array.isArray(level1Entry.value)) {\n        const newItem = { [newName]: [] };\n        level1Entry.value.push(newItem);\n      }\n    } else if (scope === 'grid') {\n      const baseEntry = gridBaseEntry.value;\n      if (!baseEntry) return;\n\n      let targetArray: any = baseEntry.value;\n      for (const idx of gridPath.value) {\n        const step = computeGridEntries(targetArray, baseEntry.path);\n        targetArray = step.values[idx];\n      }\n\n      if (Array.isArray(targetArray)) {\n        const newItem = payload.__createAsLeaf\n          ? { [newName]: payload.newValue ?? '' }\n          : { [newName]: [] };\n        targetArray.push(newItem);\n      } else if (\n        typeof targetArray === 'object' &&\n        targetArray !== null\n      ) {\n        if (payload.__createAsLeaf) {\n          (targetArray as any)[newName] = payload.newValue ?? '';\n        } else {\n          (targetArray as any)[newName] = [];\n        }\n      }\n    }\n\n    persistEditResult();\n    return;\n  }\n\n  if (id.startsWith('prefix:')) {\n    const idx = Number(id.slice(7));\n    const list = prefixList.value;\n    const item = list[idx];\n    if (item && typeof item === 'object') {\n      const oldKey = Object.keys(item)[0];\n      if (payload.newName && payload.newName !== oldKey) {\n        delete (item as any)[oldKey];\n        (item as any)[payload.newName] = payload.newValue ?? '';\n      } else if (payload.newValue != null) {\n        (item as any)[oldKey] = payload.newValue;\n      }\n    }\n    persistEditResult();\n    return;\n  }\n\n  if (id.startsWith('suffix:')) {\n    const idx = Number(id.slice(7));\n    const list = suffixList.value;\n    const item = list[idx];\n    if (item && typeof item === 'object') {\n      const oldKey = Object.keys(item)[0];\n      if (payload.newName && payload.newName !== oldKey) {\n        delete (item as any)[oldKey];\n        (item as any)[payload.newName] = payload.newValue ?? '';\n      } else if (payload.newValue != null) {\n        (item as any)[oldKey] = payload.newValue;\n      }\n    }\n    persistEditResult();\n    return;\n  }\n\n  const resolved = resolveById(quickSendData.value, id);\n  if (!resolved) return;\n  const { container, keyInContainer } = resolved;\n\n  updateContainerWithPayload({\n    container,\n    keyInContainer,\n    payload,\n  });\n\n  persistEditResult();\n}\n\n/**\n * \n *\n * @param idx - \n * @param scope - 'l1''l2'  'grid'\n */\nfunction handleCopy(idx: number, scope: 'l1' | 'l2' | 'grid') {\n  //  scope  ID\n  const id =\n    scope === 'grid'\n      ? makeIdForGridIdx(idx)\n      : scope === 'l1'\n        ? makeIdForLevel1Idx(idx)\n        : makeIdForLevel2Idx(idx);\n  if (!id) return;\n\n  const resolved = resolveById(quickSendData.value, id);\n  if (!resolved) return;\n\n  const { container, keyInContainer, path, value } = resolved;\n  const lastPathKey = _.last(path);\n  const secondLastPathKey =\n    path.length >= 2 ? path[path.length - 2] : undefined;\n\n  //  container \n  const isSingleKeyObjectCase =\n    _.isPlainObject(container) &&\n    isSingleKeyObjectUtil(container) &&\n    _.isString(keyInContainer) &&\n    (_.isNumber(secondLastPathKey) || _.isNumber(lastPathKey));\n\n  // \n  if (\n    isSingleKeyObjectCase ||\n    (_.isPlainObject(container) && _.isString(keyInContainer))\n  ) {\n    let parentContainer: any = null;\n    let arrayIndexInPath: number | null = null;\n    let isInArray = false;\n\n    // \n    if (isSingleKeyObjectCase) {\n      const arrayIndex = _.isNumber(lastPathKey)\n        ? lastPathKey\n        : _.isNumber(secondLastPathKey)\n          ? secondLastPathKey\n          : null;\n\n      if (arrayIndex !== null) {\n        const parentPath = _.isNumber(lastPathKey)\n          ? _.initial(path)\n          : path.length >= 2\n            ? path.slice(0, -2)\n            : [];\n\n        const foundParent = _.isEmpty(parentPath)\n          ? quickSendData.value\n          : getByPath(quickSendData.value, parentPath);\n\n        if (_.isArray(foundParent)) {\n          parentContainer = foundParent;\n          arrayIndexInPath = arrayIndex;\n          isInArray = true;\n        }\n      }\n    }\n\n    // \n    const oldKey = keyInContainer as string;\n    const copiedValue = _.cloneDeep(value);\n\n    // \n    let newKey = `${oldKey}_`;\n    let counter = 1;\n\n    if (isInArray && parentContainer) {\n      // \n      while (\n        parentContainer.some(\n          (item: any) =>\n            _.isPlainObject(item) &&\n            isSingleKeyObjectUtil(item) &&\n            Object.keys(item)[0] === newKey,\n        )\n      ) {\n        newKey = `${oldKey}_${counter}`;\n        counter++;\n      }\n\n      // \n      const newSingleKeyObject = { [newKey]: copiedValue };\n      const insertIndex = arrayIndexInPath! + 1;\n\n      parentContainer.splice(insertIndex, 0, newSingleKeyObject);\n    } else {\n      //  container  key\n      while (_.has(container, newKey)) {\n        newKey = `${oldKey}_${counter}`;\n        counter++;\n      }\n\n      // \n      _.set(container, newKey, copiedValue);\n    }\n  }\n\n  // \n  replaceVariables(_.cloneDeep(rawData), {\n    type: 'script',\n    script_id: getScriptId(),\n  });\n}\n\n/**\n * \n *\n * @param idx - \n * @param type - 'prefix'  'suffix'\n */\nfunction handleCopyPrefixSuffix(\n  idx: number,\n  type: 'prefix' | 'suffix',\n) {\n  const list =\n    type === 'prefix' ? prefixList.value : suffixList.value;\n  if (!Array.isArray(list) || idx < 0 || idx >= list.length) return;\n\n  const item = list[idx];\n  if (!item || typeof item !== 'object') return;\n\n  // \n  const oldKey = getPrefixSuffixKey(item);\n  const oldValue = getPrefixSuffixValue(item);\n\n  // \n  let newKey = `${oldKey}_`;\n  let counter = 1;\n\n  // \n  while (\n    list.some(\n      (it: any) =>\n        it &&\n        typeof it === 'object' &&\n        !Array.isArray(it) &&\n        Object.keys(it)[0] === newKey,\n    )\n  ) {\n    newKey = `${oldKey}_${counter}`;\n    counter++;\n  }\n\n  // \n  const copiedValue = _.cloneDeep(oldValue);\n  const newItem = { [newKey]: copiedValue };\n\n  // \n  list.splice(idx + 1, 0, newItem);\n\n  //  rawData\n  if (type === 'prefix') {\n    if (rawData.perfix) {\n      rawData.perfix = list as any;\n    } else {\n      rawData.prefix = list as any;\n    }\n  } else {\n    rawData.suffix = list as any;\n  }\n\n  // \n  replaceVariables(_.cloneDeep(rawData), {\n    type: 'script',\n    script_id: getScriptId(),\n  });\n}\n\n/**\n * \n *\n * @param idx - \n */\nfunction handleDeleteById(id: string) {\n  if (!id) return;\n\n  // \n  if (id.startsWith('prefix:')) {\n    const idx = Number(id.slice(7));\n    const list = prefixList.value;\n    if (Array.isArray(list) && idx >= 0 && idx < list.length) {\n      list.splice(idx, 1);\n      if (rawData.perfix) rawData.perfix = list as any;\n      else rawData.prefix = list as any;\n    }\n  } else if (id.startsWith('suffix:')) {\n    const idx = Number(id.slice(7));\n    const list = suffixList.value;\n    if (Array.isArray(list) && idx >= 0 && idx < list.length) {\n      list.splice(idx, 1);\n      rawData.suffix = list as any;\n    }\n  } else {\n    // l1/l2/grid \n    const resolved = resolveById(quickSendData.value, id);\n    if (!resolved) {\n      confirmDeleteId.value = null;\n      return;\n    }\n    const { container, keyInContainer, path } = resolved;\n\n    if (Array.isArray(container)) {\n      container.splice(keyInContainer as number, 1);\n    } else if (typeof container === 'object' && container !== null) {\n      delete (container as any)[keyInContainer];\n      // \n      try {\n        const containerPath = path.slice(0, -1);\n        const parentPath = containerPath.slice(0, -1);\n        const idxInParent = containerPath[containerPath.length - 1];\n        const parentContainer =\n          parentPath.length === 0\n            ? quickSendData.value\n            : getByPath(quickSendData.value, parentPath);\n        if (\n          parentContainer &&\n          Array.isArray(parentContainer) &&\n          typeof idxInParent === 'number' &&\n          container &&\n          typeof container === 'object' &&\n          Object.keys(container).length === 0\n        ) {\n          parentContainer.splice(idxInParent, 1);\n        }\n      } catch (_e) {\n        /* ignore */\n      }\n    }\n  }\n\n  replaceVariables(_.cloneDeep(rawData), {\n    type: 'script',\n    script_id: getScriptId(),\n  });\n  confirmDeleteId.value = null;\n}\n\nfunction persistEditResult() {\n  replaceVariables(rawData, {\n    type: 'script',\n    script_id: getScriptId(),\n  });\n  closeEditModal();\n}\n\nfunction closeEditModal() {\n  editOpen.value = false;\n  editNodeId.value = null;\n  editSnapshot.value = null;\n}\n\nfunction togglePrefixList() {\n  showPrefixList.value = !showPrefixList.value;\n  if (showPrefixList.value) showSuffixList.value = false;\n}\n\nfunction toggleSuffixList() {\n  showSuffixList.value = !showSuffixList.value;\n  if (showSuffixList.value) showPrefixList.value = false;\n}\n\nfunction getPrefixSuffixKey(item: any): string {\n  if (item && typeof item === 'object' && !Array.isArray(item)) {\n    const keys = Object.keys(item);\n    if (keys.length > 0) return keys[0];\n  }\n  return '';\n}\n\nfunction getPrefixSuffixValue(item: any): string {\n  if (item && typeof item === 'object' && !Array.isArray(item)) {\n    const keys = Object.keys(item);\n    if (keys.length > 0) {\n      const value = item[keys[0]];\n      return typeof value === 'string' ? value : String(value);\n    }\n  }\n  return '';\n}\n\n/**\n * \n *\n * @param item - \n * @param idx - \n */\nfunction onPrefixItemClick(_item: any, idx: number) {\n  if (editMode.value) return;\n\n  if (selectedPrefixIndexes.value.has(idx)) {\n    selectedPrefixIndexes.value.delete(idx);\n  } else {\n    selectedPrefixIndexes.value.add(idx);\n  }\n\n  selectedPrefixIndexes.value = new Set(selectedPrefixIndexes.value);\n  //  watch applyMacroSubstitution\n}\n\n/**\n * \n *\n * @param item - \n * @param idx - \n */\nfunction onSuffixItemClick(_item: any, idx: number) {\n  if (editMode.value) return;\n\n  if (selectedSuffixIndexes.value.has(idx)) {\n    selectedSuffixIndexes.value.delete(idx);\n  } else {\n    selectedSuffixIndexes.value.add(idx);\n  }\n\n  selectedSuffixIndexes.value = new Set(selectedSuffixIndexes.value);\n  //  watch applyMacroSubstitution\n}\n\nfunction openEditPrefixSuffix(\n  idx: number,\n  type: 'prefix' | 'suffix',\n) {\n  const list =\n    type === 'prefix' ? prefixList.value : suffixList.value;\n  const item = list[idx];\n  if (!item) return;\n  const key = getPrefixSuffixKey(item);\n  const value = getPrefixSuffixValue(item);\n  editNodeId.value = `${type}:${idx}`;\n  editSnapshot.value = {\n    title: key,\n    name: key,\n    canRename: true,\n    isLeaf: true,\n    value: value,\n    children: [],\n  };\n  editOpen.value = true;\n}\n\n/**\n * /\n *\n * @param type - 'prefix'  'suffix'\n */\nfunction openCreatePrefixSuffix(type: 'prefix' | 'suffix') {\n  editNodeId.value = `__create__:${type}`;\n  editSnapshot.value = {\n    title: type === 'prefix' ? '' : '',\n    name: '',\n    canRename: true,\n    isLeaf: true,\n    value: '',\n    children: [],\n    isCreate: true,\n  };\n  editOpen.value = true;\n}\n\nfunction sendQuickText() {\n  const finalText = text.value;\n\n  triggerSlash(`/send ${finalText}|/trigger`);\n  if (onClose) onClose();\n}\n\n/**\n * \n */\nfunction appendSillyTavernText() {\n  const STText = $('#send_textarea').val();\n  if (STText === '' || STText === null) {\n    tavernText.value = '';\n    applyMacroSubstitution();\n    return;\n  }\n  tavernText.value = String(STText);\n  applyMacroSubstitution();\n}\n</script>\n\n<style lang=\"css\" scoped>\n.tab-active {\n  font-weight: 700;\n}\n\n.tab-active:before {\n  content: '';\n  display: block;\n  position: absolute;\n  border-top: none;\n  border-bottom: 2px solid var(--quick-send-primary-color);\n  width: 100%;\n  left: 0;\n  bottom: 0;\n}\n\n.auto-grid {\n  grid-template-columns: repeat(\n    auto-fit,\n    minmax(var(--auto-grid-min, 2.5rem), 1fr)\n  );\n}\n\n.auto-grid-item {\n  min-width: var(--auto-grid-min, 2.5rem);\n  display: flex;\n  align-items: center;\n  gap: 0.25rem;\n  width: 100%;\n}\n\n.auto-grid-item .grid-label {\n  flex: 1 1 auto;\n  min-width: 0;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n\n.tippy::after {\n  content: '';\n  position: absolute;\n  bottom: -5px;\n  left: 50%;\n  transform: translateX(-50%);\n  width: 0;\n  height: 0;\n  border-left: 6px solid transparent;\n  border-right: 6px solid transparent;\n  border-top: 6px solid var(--quick-send-text);\n}\n</style>\n"],"sourceRoot":""}]);
